<template id="">
  <div class="m-miaokai">
    <header>
      <h1></h1>
    </header>
    <section>
      <div id="wrapper">
        <div id="index-scroll">
            <div class="head hide">
              <img src="/images/arrow.png" width="40" height="40"/>
              <span>...</span>
            </div>
            <ul>
              <li v-for="item in list" :data-id="item.id">
                <router-link :to="'/detail/'+item.id">
                  <div>
                      <i><img v-bind:src="item.imgUrl" /></i>
                      <b>{{item.content}}</b>
                  </div>
                  <div>
                    <div>
                      <p>:<span>{{item.progress}}</span></p>
                      <p><span :style="'width:'+item.progress"></span></p>
                    </div>
                    <div></div>
                  </div>
                </router-link>
              </li>
            </ul>
            <div class="foot hide">
              <img src="/images/arrow.png" width="40" height="40"/>
              <span>...</span>
            </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
var common=require('../utils/util.common.js');

module.exports={   //
  data:function(){
    return{
      list:[]
    }
  },
  mounted:function(){
    fetch('/api/goods').then(response => response.json()).then(res => {

        var that = this;
        that.list = res;
        setTimeout(function(){
          common.scroll(that);
        })
    }).catch(e => console.log("Oops, error", e));
  }
  // methods: {
  //
  // }
}
</script>



